<template>
  <section>
    <v-dropdown class="tag-wrap" position="left">
      <i class="fa fa-tags"> </i>
      <v-dropmenu slot="dropdown" :options="tagOptions" option-key="_id" v-model="tags"></v-dropmenu>
      <v-dropmenu slot="dropdown" :options="['']" @click.native="showDialog"></v-dropmenu>
    </v-dropdown>
  </section>
</template>

<script>
import { mapState, mapActions } from 'vuex'

export default {
  name: 'tagSection',
  props: {
    // 
    tags: Array
  },
  computed: mapState({
    // 
    tagOptions: ({ tag }) => tag.tagList
  }),
  methods: {
    showDialog () {
      this.$modal.prompt({ title: '' }).then((data) => {
        this.addTag(data)
      }, () => { })
    },
    ...mapActions(['getTagList', 'addTag'])
  },
  created () {
    this.getTagList()
  }
}
</script>


<style scoped>
.tag-wrap{
  border:1px solid #ccc;
  border-radius:4px;
}
</style>



